<template>
  <div :class="uploadWrapClass">
    <label @click="handleClick">
      <slot></slot>
      <input type="file" @change="change" ref="input" name="file"
        :multiple="multiple"
      >
    </label>
    <slot name="tip"></slot>
      <transition-group tag="ul" :class="fileListClass" name="list">
        <li v-for="(file,index) in fileList" :key="index">
          <Icon type="order"/> <span class="file-list-name">{{ file.name }}</span>
          <Icon :type="resultIcon" class="icon-result"/> 
        </li>
      </transition-group>
  </div>
</template>

<script>
import { prefixCls } from "@/utils/config";
import Icon from "@/components/icon";
export default {
  name: "upload",
  components: {
    Icon
  },
  props: {
    action: {
      type: String,
      required: true
    },
    multiple: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      default() {
        return {};
      }
    },
    accept: String,
    defaultFileList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  computed: {
    uploadWrapClass() {
      return [`${prefixCls}-upload`];
    },
    fileListClass() {
      return [`${prefixCls}-file-list`];
    }
  },
  data() {
    return {
      fileList: this.defaultFileList,
      resultIcon: "success"
    };
  },
  methods: {
    handleClick() {
      this.$refs.input.click();
    },
    change(e) {
      const files = e.target.files;
      if (this.multiple) {
        this.fileList = [...this.fileList, ...files];
      } else {
        this.fileList = [files[0]];
      }
      console.log(this.fileList);
    },
    post(file) {
      let formData = new FormData();
      formData.append(this.name, file);
      ajax({
        headers: this.headers,
        withCredentials: this.withCredentials,
        file: file,
        data: this.data,
        filename: this.name,
        action: this.action,
        onProgress: e => {
          this.handleProgress(e, file);
        },
        onSuccess: res => {
          this.handleSuccess(res, file);
        },
        onError: (err, response) => {
          this.handleError(err, response, file);
        }
      });
    }
  }
};
</script>


